import React, { Component } from "react";
import Alert from "@material-ui/lab/Alert";
import { MDBAlert } from "mdbreact";
import * as Yup from "yup";
import axios from "../../axios";
import { Formik, Form, Field } from "formik";
import {
  Button,
  Container,
  Typography,
  Grid,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import BeatLoader from "react-spinners/BeatLoader";
import { MDBTypography } from "mdbreact";
class Scholarshipedit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formdata: null,
      loadding: false,
      duplicate: false,
      invalidregno: false,
    };
  }
  render() {
    return (
      <React.Fragment>
        <Container maxWidth="md">
          <Typography component="div" style={{ height: "5vh" }} />
          <MDBTypography
            tag="h1"
            variant="h1-responsive"
            className="text-center"
            colorText="teal"
          >
            Student Financial Assistance
          </MDBTypography>
          <div className="text-center">
            <Formik
              initialValues={{ RegistrationNo: "" }}
              validationSchema={Yup.object({
                RegistrationNo: Yup.string()
                  .max(15, "Invalid Registration Number")
                  .min(10, "Invalid Registration Number")
                  .required("Enter Student Registartion Number"),
              })}
              onSubmit={(values, { setSubmitting }) => {
                this.setState({
                  loadding: true,
                  duplicate: false,
                  invalidregno: false,
                });
                setTimeout(() => {
                  setSubmitting(true);
                  //alert(JSON.stringify(values, null, 2));
                  axios
                    .get("api/scholarship/search/" + values.RegistrationNo, {})
                    .then((res) => {
                      if (res.data.flag === "1") {
                        setSubmitting(false);
                        this.setState({
                          loadding: false,
                          invalidregno: true,
                        });
                      } else {
                        const data = res.data;
                        const exam = data[0];
                        setSubmitting(false);
                        this.props.data(exam, 2);
                        this.setState({
                          loadding: false,
                        });
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                      setSubmitting(false);
                    });
                }, 500);
              }}
            >
              {({ submitForm, isSubmitting, values }) => (
                <Form>
                  <Grid container spacing={3}>
                    <Grid item lg={3} xs={1}>
                      {" "}
                    </Grid>
                    <Grid item lg={6} xs={10}>
                      {this.state.invalidregno ? (
                        <Alert severity="error">
                          {" "}
                          Invalid Registration No. Enter a Valid Registration No{" "}
                        </Alert>
                      ) : null}
                    </Grid>
                    <Grid item lg={3} xs={1}>
                      {" "}
                    </Grid>
                    <Grid item lg={4} xs={1}>
                      {" "}
                    </Grid>

                    <Grid item lg={4} xs={10}>
                      <Field
                        component={TextField}
                        name="RegistrationNo"
                        type="text"
                        label="Registration No"
                        variant="outlined"
                        fullWidth
                      />
                    </Grid>
                    <Grid item lg={4} xs={1}>
                      {" "}
                    </Grid>
                    <Grid item lg={4} xs={1}>
                      {" "}
                    </Grid>
                    <Grid item lg={4} xs={10}>
                      <BeatLoader
                        size={20}
                        margin={5}
                        color={"#154883"}
                        loading={this.state.loadding}
                      />
                      {this.state.loadding ? null : (
                        <Button
                          variant="contained"
                          color="primary"
                          disabled={isSubmitting}
                          onClick={submitForm}
                        >
                          Get Form
                        </Button>
                      )}
                    </Grid>
                    <Grid item lg={4} xs={12}>
                      {" "}
                    </Grid>
                  </Grid>
                </Form>
              )}
            </Formik>
          </div>
          <Typography component="div" style={{ height: "30vh" }} />
        </Container>
      </React.Fragment>
    );
  }
}

export default Scholarshipedit;
