import React, { useState } from "react";
import { DropzoneArea } from "material-ui-dropzone";
import BeatLoader from "react-spinners/BeatLoader";
import * as Yup from "yup";
import Alert from "@material-ui/lab/Alert";
import axios from "../../axios";
import { Formik, Form, Field } from "formik";
import { TextField, Select } from "formik-material-ui";
import {
  Button,
  Container,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import { MDBTypography } from "mdbreact";

export default function StForm({ data, formstate }) {
  const [state, setState] = React.useState({
    formdata: data,
  });

  const defaultValidation = Yup.object({});
  const [loadding, setloadding] = useState(false);

  return (
    <React.Fragment>
      <Container maxWidth="md">
        <Typography component="div" style={{ height: "5vh" }} />

        <MDBTypography
          tag="h1"
          variant="h1-responsive"
          className="text-center"
          colorText="teal"
        >
          {" "}
          Financial Assistance Form
        </MDBTypography>
        <Typography component="div" style={{ height: "5vh" }} />

        <div className="text-left">
          <Formik
            initialValues={state.formdata}
            validationSchema={defaultValidation}
            onSubmit={(values, { setSubmitting }) => {
              setTimeout(() => {
                axios
                  .post(
                    "api/scholarship/edit/" + values.RegistrationNo,
                    JSON.stringify(values),
                    {
                      headers: {
                        // Overwrite Axios's automatically set Content-Type
                        "Content-Type": "application/json",
                        Accept: "application/json",
                      },
                    }
                  )
                  .then((res) => {
                    if (res.status === 200 && res.data.flag === "success") {
                      alert("updated Sucessfully");
                      formstate(1)
                    }
                    if (res.data.login === "fail") {
                      alert("ELSE IF:" + res.data.msg);
                    }
                    if (res.status === 401 && res.data.login === "fail") {
                    }
                  })
                  .catch(function (error) {
                    alert("Error occured:" + error);
                    setSubmitting(false);
                    setloadding(false);
                  });
              }, 500);

              setSubmitting(false);
              setloadding(false);
            }}
          >
            {({ errors, submitForm, isSubmitting }) => (
              <Form>
                <Grid container spacing={3}>
                  <Grid item lg={12} xs={12}>
                    <MDBTypography
                      note
                      noteColor="success"
                      noteTitle="Academic Qualification "
                    >
                      {" "}
                    </MDBTypography>
                  </Grid>
                  <Grid item lg={2} xs={6}>
                    <Field
                      component={TextField}
                      name="st_reg_no"
                      type="text"
                      label="Registration No"
                      variant="outlined"
                      fullWidth
                      disabled
                      margin="dense"
                    />
                  </Grid>
                  <Grid item lg={2} xs={6}>
                    <Field
                      component={TextField}
                      name="Roll_no"
                      type="text"
                      label="Class Roll No"
                      variant="outlined"
                      fullWidth
                      disabled
                      margin="dense"
                    />
                  </Grid>
                  <Grid item lg={4} xs={6}>
                    <Field
                      component={TextField}
                      name="Course_name"
                      type="text"
                      label="Course"
                      variant="outlined"
                      fullWidth
                      disabled
                      margin="dense"
                    />
                  </Grid>
                  <Grid item lg={2} xs={6}>
                    <Field
                      component={TextField}
                      name="batch"
                      type="text"
                      label="Batch"
                      variant="outlined"
                      fullWidth
                      disabled
                      margin="dense"
                    />
                  </Grid>

                  <Grid item lg={12} xs={12}>
                    <MDBTypography
                      note
                      noteColor="success"
                      noteTitle="Personal Details "
                    >
                      {" "}
                    </MDBTypography>
                  </Grid>

                  <Grid item lg={4} xs={12}>
                    <Field
                      component={TextField}
                      name="firstName"
                      type="text"
                      label="First Name"
                      variant="outlined"
                      disabled
                      fullWidth
                    />
                  </Grid>
                  <Grid item xs={12} lg={4}>
                    <Field
                      component={TextField}
                      name="middleName"
                      type="text"
                      label="Middle Name"
                      variant="outlined"
                      disabled
                      fullWidth
                    />
                  </Grid>
                  <Grid item xs={12} lg={4}>
                    <Field
                      component={TextField}
                      name="lastName"
                      type="text"
                      label="Last Name "
                      variant="outlined"
                      disabled
                      fullWidth
                    />
                  </Grid>

                  <Grid item lg={4} xs={12}>
                    <Field
                      component={TextField}
                      name="fFirstName"
                      type="text"
                      label="Father's First Name"
                      variant="outlined"
                      fullWidth
                      disabled
                    />
                  </Grid>
                  <Grid item lg={4} xs={12}>
                    <Field
                      component={TextField}
                      name="fMiddleName"
                      type="text"
                      label="Father's Middle Name"
                      variant="outlined"
                      disabled
                      fullWidth
                    />
                  </Grid>

                  <Grid item xs={12} lg={4}>
                    <Field
                      component={TextField}
                      name="fLastName"
                      type="text"
                      label="Father's Last Name"
                      variant="outlined"
                      disabled
                      fullWidth
                    />
                  </Grid>

                  <Grid item lg={12} xs={12}>
                    <MDBTypography
                      note
                      noteColor="success"
                      noteTitle="Father / Guardian Income "
                    ></MDBTypography>
                  </Grid>
                  <Grid item xs={12} lg={4}>
                    <Field
                      component={TextField}
                      name="pOccupation"
                      type="text"
                      label="Guardian/Father Occupation"
                      variant="outlined"
                      fullWidth
                    />
                  </Grid>
                  <Grid item xs={12} lg={4}>
                    <Field
                      component={TextField}
                      name="fgIncome"
                      type="text"
                      label="Guardian/Father Income"
                      variant="outlined"
                      fullWidth
                    />
                  </Grid>
                  <Grid item xs={12} lg={4}>
                    <FormControl variant="outlined" fullWidth>
                      <InputLabel htmlFor="outlined-age-native-simple">
                        Catagory Applied
                      </InputLabel>
                      <Field
                        component={Select}
                        value={state.formdata.catagory_applied}
                        name="catagory_applied"
                        label="Catagory Applied"
                        inputProps={{
                          id: "outlined-age-native-simple",
                        }}
                      >
                        <MenuItem value={"Orphan/Broken Family"}>
                          Orphan/Broken Family
                        </MenuItem>
                        <MenuItem value={"Specially Enabled"}>
                          Specially Enabled
                        </MenuItem>
                        <MenuItem value={"OBC/SC/ST"}>OBC/SC/ST</MenuItem>
                        <MenuItem value={"AAY/PHH"}>AAY/ PHH</MenuItem>
                      </Field>
                    </FormControl>
                  </Grid>

                  <Grid item lg={12} xs={12}>
                    <MDBTypography
                      note
                      noteColor="success"
                      noteTitle="Bank Details "
                    >
                      Enter The Bank Details of the Candidate{" "}
                    </MDBTypography>
                  </Grid>
                  <Grid item lg={3} xs={12}>
                    <Field
                      component={TextField}
                      name="bankName"
                      type="text"
                      label="Bank Name"
                      variant="outlined"
                      fullWidth
                      error={errors.bankname ? true : false}
                    />
                  </Grid>
                  <Grid item lg={3} xs={12}>
                    <Field
                      component={TextField}
                      name="bankBranch"
                      type="text"
                      label="Branch Name"
                      variant="outlined"
                      fullWidth
                      error={errors.bankbranch ? true : false}
                    />
                  </Grid>
                  <Grid item lg={3} xs={12}>
                    <Field
                      component={TextField}
                      name="accountNumber"
                      type="text"
                      label="Bank A/C No"
                      variant="outlined"
                      fullWidth
                      error={errors.accountno ? true : false}
                      touched
                    />
                  </Grid>
                  <Grid item lg={3} xs={1}></Grid>

                  <Grid item lg={3} xs={1}></Grid>

                  <Grid item lg={12} xs={12}>
                    <Button
                      variant="contained"
                      color="primary"
                      disabled={isSubmitting}
                      onClick={submitForm}
                    >
                      Update
                    </Button>
                  </Grid>
                </Grid>
              </Form>
            )}
          </Formik>
        </div>
        <Typography component="div" style={{ height: "20vh" }} />
      </Container>
    </React.Fragment>
  );
}
