import React, { useState } from "react";
import StForm from "./Stform";
import Scholarshipedit from "./Scholarshipedit";
export default function Financialaid() {
  const [formreg, setformreg] = useState(1);
  const [formdata, setformdata] = useState();
  function getstdata(exam, st) {
    setformdata(exam);
    setformreg(st);
  }
  function formstate(st, reg) {
    setformreg(st);
  }

  switch (formreg) {
    case 1:
      return <Scholarshipedit data={getstdata} />;
    case 2:
      return <StForm data={formdata} formstate={formstate} />;
    default:
      return <Scholarshipedit data={getstdata} />;
  }
}
